$green: #447b28;
$black: #262525;

html,
body {
  background-color: $black;
  margin: 0;
  color: #eee;
  font-family: "Roboto", sans-serif;
  font-size: 15px;

  *:focus,
  *:active {
    outline: none;
  }

  * {
    margin: 0;
    padding: 0;
    box-sizing: border-box;
  }
}

.header {
  background-color: $green;
  height: 60px;
  display: flex;
  align-items: center;
  color: #eee;

  > div {
    padding: 10px 20px;
    padding-right: 5px;

    img {
      height: 40px;
    }
  }
  > div:last-child {
    padding: 10px;
  }
}

.container {
  display: flex;
  flex-flow: row;
  margin: 0 auto;
  padding: 0;
  opacity: 0;
  margin-top: 0;
  animation: opacityAnim 7s forwards;
  max-width: 900px;

  > div:first-child {
    text-align: right;
    overflow: hidden;
    padding: 40px 0;
    padding-bottom: 40px;
    flex: 0 0 150px;

    img {
      width: 100px;
      margin-right: 40px;
    }

    > div {
      padding-bottom: 20px;
      margin-right: 35px;
    }
  }

  > div:last-child {
    overflow: auto;
    flex: 1;
    padding: 40px 10px;

    > div {
      text-align: left;
      background-color: #333;
      padding: 0 30px;
      overflow: hidden;
      border-bottom: solid 2px $green;
      margin-bottom: 20px;

      > div {
        padding-bottom: 15px;
        display: flex;

        > span:first-child {
          flex: 0 0 20px;
          text-align: center;
          padding-right: 10px;
        }
      }
    }
  }
}

.portfolio {
  flex-flow: column;
  flex-wrap: wrap;
  position: relative;
  min-height: 500px;
  display: flex;
  flex-flow: column;

  > div {
    flex: 1 1 500px;
    position: relative;
    background-size: contain;
    background-repeat: no-repeat;
    background-position: center;
    margin-bottom: 20px;
  }

  .imgPreview {
    transition: all ease 0.3s;
    position: relative;

    .imgsToShow {
      div {
        display: inline-block;
        background-size: contain;
        background-position: center;
        background-repeat: no-repeat;
        position: absolute;
        top: 0;
        left: 0;
        right: 0;
        bottom: 0;
        transition: all ease-in-out 1s;
        opacity: 1;
        transform: scale(1);

        // > section {
        //   position: absolute;
        //   bottom: 0;
        //   left: 0;
        //   width: 0;
        //   opacity: 1;
        //   height: 3px;
        //   background: red;
        //   transition: width linear 7s;
        //   transition-delay: 1s;
        // }

        &:nth-child(2) {
          opacity: 0;
          // > section {
          //   width: 100%;
          // }
        }

        &:first-child {
          left: -100%;
          right: 100%;
          opacity: 0.2;
          transform: scale(0.5);
          // > section {
          //   background: none;
          // }
        }
        &:last-child {
          left: 100%;
          right: -100%;
          opacity: 0.2;
          transform: scale(0.5);
          // > section {
          //   background: none;
          // }
        }
      }
    }

    &.imgReady {
      .imgsToShow {
        div {
          &:nth-child(2) {
            opacity: 1;
          }
        }
      }
    }

    .imagePlay {
      position: absolute;
      display: flex;
      align-items: center;
      justify-content: center;
      top: 50%;
      left: 50%;
      transform: translateX(-50%) translateY(-50%);
      z-index: 999;
      font-size: 30px;
      display: flex;
      height: 60px;
      width: 60px;
      text-align: center;
      padding: 15px;
      border-radius: 50%;
      border: solid 3px #fff;
      background-color: $black;
      cursor: pointer;
      transition: all ease-in-out 0.3s;
      &:hover {
        background-color: $green;
      }
    }

    &:hover {
      .imagePlay {
        opacity: 1;
      }
    }

    .imgNav {
      display: flex;
      flex-flow: row;
      align-items: center;
      position: absolute;
      left: -15px;
      right: -15px;
      top: 50%;
      transform: translateY(-50%);
      height: auto;
      margin: 0;
      padding: 0 !important;

      > div {
        flex: 0;
        padding: 15px 10px;
        cursor: pointer;
        background-color: rgba(1, 1, 1, 0.7);
        transition: all 0.3s;

        &:hover {
          background-color: $green;
        }
        &:nth-child(2) {
          flex: 1;
          text-align: center;
          opacity: 1;
          pointer-events: none;
          background-color: none;
          background: none;
          transition: all linear 500ms;
          transition-delay: 1s;

          &.imgReady {
            opacity: 0;
          }
        }
      }
    }
  }
}

.about,
.contact,
.notfound {
  display: flex;
  flex-flow: column;
  flex-wrap: wrap;
  min-height: 500px;
}

.footer {
  text-align: left;
  color: #ddd;
  padding-bottom: 20px;
  font-size: 13px;
}

@keyframes opacityAnim {
  0% {
    opacity: 0;
    margin-top: -20px;
  }

  80% {
    opacity: 0;
    margin-top: -20px;
  }

  100% {
    opacity: 1;
    margin-top: 0;
  }
}

a {
  color: lightblue;
  text-decoration: none;

  &.active {
    text-decoration: none;
    color: #fff;
  }
}

a:not(.active):hover {
  text-decoration: underline;
}

hr {
  margin: 20px 0;
  border-color: #555;
  border-width: 0.5px;
}

h3 {
  font-size: 23px;
  margin: 0 -30px;
  padding: 15px 30px;
  margin-bottom: 25px;
  font-weight: 100;
  font-family: "Montserrat", sans-serif;
  background-color: $green;
  display: flex;
  align-items: center;
}

i.fa {
  color: #bbb;
}

#imgContainer {
  position: relative;
  overflow: hidden;
  margin: 0 -30px;
  padding-bottom: 0;
  margin-top: 10px;
  background-size: cover;
  background-position: center;
  background-repeat: no-repeat;

  // #imgFancy {
  //   position: absolute;
  //   left: 0;
  //   right: 0;
  //   bottom: 0;
  //   top: 0;
  //   z-index: 1;
  //   transition: all 0.3s;
  // }

  // #imgFancy:hover {
  //   transform: scale(1.25);
  // }
}

#logoContainer {
  z-index: 10;
  position: absolute;
  left: -31px;
  bottom: -33px;
  text-align: center;

  #logoLayer1 {
    border: solid 8px #ffffff;
    padding: 22px;
    display: inline-block;

    transform: scale(0.7) rotateZ(0deg);
    animation: pulse 2s forwards;
    opacity: 1;
  }

  #logoLayer2 {
    width: 50px;
    height: 50px;
    background-color: $green;
  }
}

@keyframes pulse {
  0% {
    opacity: 1;
    transform: scale(0.7) rotateZ(0);
  }

  33% {
    transform: scale(0.5) rotateZ(180deg);
    opacity: 1;
  }

  66% {
    transform: scale(0.7) rotateZ(360deg);
    opacity: 1;
  }

  100% {
    transform: scale(0.3) rotateZ(540deg);
    opacity: 0;
  }
}

#svgLogoAnim {
  position: fixed;
  width: 100%;
  top: 30%;
  left: 30px;
  height: 200px;
  pointer-events: none;
  opacity: 1;
  animation: logoAnim 6s forwards;
}

@keyframes logoAnim {
  0% {
    opacity: 1;
    top: 30%;
  }
  40% {
    opacity: 1;
  }
  80% {
    opacity: 1;
  }
  100% {
    top: 15%;
    opacity: 0;
  }
}

#svgLogoAnim > div#svgImgs {
  width: 140px;
  height: 200px;
  margin: auto;
  position: relative;
}

#svgLogoAnim img {
  height: 200px;
  position: absolute;
  left: 0;
  top: 0;
}

#svgLogoAnim section {
  position: absolute;
  left: 20px;
  bottom: -25px;
  opacity: 0;
  color: #555;
  animation: logoLabel 2s forwards;
  animation-delay: 4s;
  font-family: "Montserrat", sans-serif;
}

@keyframes logoLabel {
  0% {
    opacity: 0;
    color: #555;
    left: 20px;
  }
  50% {
    opacity: 0;
    color: #555;
    left: 20px;
  }
  100% {
    opacity: 1;
    color: #fff;
    left: 0;
  }
}

img#box1,
img#box2 {
  animation: imgBox 2.2s forwards;
  opacity: 0;
}

@keyframes imgBox {
  0% {
    opacity: 0;
  }
  66% {
    opacity: 0;
  }
  100% {
    opacity: 1;
  }
}

img#box3 {
  clip-path: polygon(0 100%, 100% 100%, 100% 0, 0 0);
  animation: imgBox3 3s forwards;
}

@keyframes imgBox3 {
  0% {
    clip-path: polygon(0 100%, 100% 100%, 100% 100%, 0% 100%);
  }
  60% {
    clip-path: polygon(0 100%, 100% 100%, 100% 100%, 0% 100%);
  }
  100% {
    clip-path: polygon(0 100%, 100% 100%, 100% 0, 0 0);
  }
}

img#petal1 {
  animation: imgPetal1 3s forwards;
  animation-delay: 1s;
  opacity: 0;
  margin-left: -13px;
}

img#petal2 {
  animation: imgPetal1 3s forwards;
  animation-delay: 1.5s;
  opacity: 0;
  margin-left: -13px;
}

img#petal3 {
  animation: imgPetal1 3s forwards;
  animation-delay: 2s;
  opacity: 0;
  margin-left: -13px;
}

@keyframes imgPetal1 {
  0% {
    opacity: 0;
    margin-left: -13px;
  }
  60% {
    opacity: 0;
    margin-left: -13px;
  }
  100% {
    opacity: 1;
    margin-left: 0;
  }
}

ul {
  list-style: none;
  text-align: left;
  margin: 0;
  margin-top: -5px;
  padding: 30px 0;
  border-left: solid 3px #fefefe;
  display: inline-block;
  width: 90px;

  li {
    padding: 0;
    cursor: pointer;
    color: #fefefe;
    margin-bottom: 10px;

    a {
      display: block;
      padding: 10px 10px;
      color: #fefefe;
      text-decoration: none;
      font-family: "Montserrat", sans-serif;
      transition: all ease 0.3s;
      border-bottom-right-radius: 20px;

      &:hover {
        text-decoration: none !important;
        background-color: $green;
        color: #ffffff;
      }

      &.active {
        pointer-events: none;
        background-color: #fefefe;
        color: $black;
        color: $black;
        text-decoration: none;
      }
    }
  }
}

.mobileMenu {
  position: fixed;
  bottom: -100px;
  left: -100px;
  background-color: red;
  display: flex;
  padding: 10px;
  border-radius: 50%;
  border: solid 3px red;
  cursor: pointer;
  transition: all ease 0.6s;
  opacity: 1;
  animation: mobileMenuAnim 7s;
  z-index: 999999;

  img {
    height: 30px;
    width: 30px;
  }

  i {
    color: #fff;
    font-size: 25px;
    height: 25px;
    width: 25px;
    text-align: center;
  }

  &.openMenu {
    bottom: -100px;
    pointer-events: none;
  }
}

@keyframes mobileMenuAnim {
  0% {
    opacity: 0;
    bottom: -100px;
    background-color: red;
    border-color: red;
  }

  80% {
    opacity: 0;
    bottom: -100px;
    background-color: red;
    border-color: red;
  }

  100% {
    opacity: 1;
    bottom: 30px;
    background-color: red;
    border-color: red;
  }
}

@keyframes mobileMenuPulseAnim {
  0% {
    transform: rotate(0);
  }

  70% {
    transform: rotate(180deg);
  }

  100% {
    transform: rotate(360deg);
  }
}

@media screen and (max-width: 760px) {
  body {
    padding: 0 0;
    position: fixed;
    top: 0;
    right: 0;
    left: 0;
    bottom: 0;
  }

  h3 {
    padding-top: 25px;
  }

  .footer {
    text-align: center;
  }

  .container {
    height: 100%;
    padding-top: 0;
    position: fixed;
    top: 0;
    right: 0;
    left: 0;
    bottom: 0;

    &.openMenu {
      overflow: hidden;
    }
  }

  .container > div {
    flex: 1;
    display: flex;
    flex-flow: column;
  }

  .container > div:first-child {
    margin-right: 0;
    position: fixed;
    z-index: 100;
    left: -180px;
    top: 0;
    bottom: 0;
    width: 170px;
    overflow: auto;
    text-align: left;

    background-color: rgba(1, 1, 1, 0.8);
    backdrop-filter: blur(5px);
    padding-top: 30px;
    padding-left: 20px;
    opacity: 0.5;
    transition: opacity ease 0.6s;
    transition-property: opacity, left;
    margin-right: 50px;

    ul {
      margin-left: 15px;
      margin-top: 0;
    }

    &.openMenu {
      opacity: 1;
      left: 0;
    }
  }

  .container > div:last-child {
    padding: 0;
    transition: all ease 0.6s;
    opacity: 1;

    &.openMenu {
      transform: translateX(170px) scale(0.9);
      opacity: 0.6;
    }

    > div {
      flex: 1;
      overflow: auto;

      display: flex;
      flex-flow: column;

      #imgContainer {
        flex: 1;
        min-height: 200px;
      }

      &.portfolio {
        overflow: hidden;
      }
    }
  }

  .mobileOverlay {
    position: fixed;
    left: 0;
    top: 0;
    bottom: 0;
    right: 0;
    background-color: rgba(1, 1, 1, 0.5);
    opacity: 0;
    pointer-events: none;
    transition: opacity ease 0.6s;
    transition-property: opacity, left;

    &.openMenu {
      pointer-events: all;
    }
  }

  .mobileMenu {
    display: flex;
    bottom: 30px;
    left: 30px;
    > img {
      animation: mobileMenuPulseAnim 3s infinite;
    }
  }
}
